<template>
    <my-page :loading="isLoading">
        <template #heading>{{ title }}</template>

        <div class="comparison">
            <table class="comparison__table">
                <thead>
                    <tr>
                        <th></th>
                        <th>{{ $t('FREE') }}</th>
                        <th>PLUS</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>{{ $t('APP_INSTALLATIONS') }}</td>
                        <td>{{ $t('N_PER_WEEK', {n: 10}) }}</td>
                        <td>{{ $t('UNLIMITED') }}</td>
                    </tr>
                    <tr>
                        <td>
                            <html-with-links
                                tag="span"
                                :html="$t('STORAGE_FOR_IPA_LIBRARY', {href: '/my/apps'}) + '*'"
                            />
                        </td>
                        <td>1 GB</td>
                        <td>15 GB</td>
                    </tr>
                    <tr>
                        <td>{{ $t('MAX_UPLOAD_SIZE') }}</td>
                        <td>256 MB</td>
                        <td>4 GB</td>
                    </tr>
                    <tr>
                        <td>
                            <html-with-links
                                tag="span"
                                :html="$t('ACCESS_TO_APP_REQUESTS', {href: '/requests'})"
                            />
                        </td>
                        <td><Icon icon="tabler:x" class="no-icon" /></td>
                        <td><Icon icon="tabler:check" class="yes-icon" /></td>
                    </tr>
                    <tr>
                        <td>{{ $t('NO_ADS') }}</td>
                        <td><Icon icon="tabler:x" class="no-icon" /></td>
                        <td><Icon icon="tabler:check" class="yes-icon" /></td>
                    </tr>
                </tbody>
            </table>
            <p class="comparison__footnote">* {{ $t('STORAGE_FOR_IPA_LIBRARY_FOOTNOTE') }}</p>
        </div>

        <sideloading-options v-if="!hasPlus" :options="plusOptions" hide-name />
        <html-with-links v-else class="g-text toast" tag="p" :html="$t('YOU_ALREADY_HAVE_PLUS', {subscriptionsLink: '/my/billing'})" />

        <html-with-links class="description g-text" :html="$t('PLUS_FEATURES_DESC')"></html-with-links>

        <template #sidebar></template>
    </my-page>
</template>

<script>
import { Icon } from '@iconify/vue2';
import SideloadingOptions from '@/components/SideloadingOptions.vue';
import { mapGetters } from 'vuex';
import MyPage from './components/MyPage.vue';
import HtmlWithLinks from '../../components/HtmlWithLinks.vue';

export default {
  components: {
    MyPage,
    Icon,
    SideloadingOptions,
    HtmlWithLinks,
  },
  data() {
    return {
      isLoading: true,
      plusOptions: [],
    };
  },
  computed: {
    ...mapGetters(['hasPlus']),
    title() {
      return `${this.BRAND_CONFIG.name} PLUS`;
    },
  },
  created() {
    this.$api.getSideloadingOptions('plus').then((response) => {
      this.plusOptions = response;
    }).finally(() => {
      this.isLoading = false;
    });
  },
};
</script>

